* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
}

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.box {
  width: 30%;
  padding: 0.5rem;
  border-radius: 10px;
  text-align: center;
  margin: 0.5rem;
  background-color: rgb(143, 248, 241);
}

.input {
  width: 60%;
  padding: 0.5rem;
  border: 1px solid #000;
  border-radius: 5px;
  display: block;
  margin: 2rem auto;
}
